import React, {Component} from 'react';

const api = 'https://api.themoviedb.org/3'
const apiKey = 'a9632aa4c0a084cd40a2f5f911739ec0'

class MovieInfo extends Component{
    
    state = {
        movieInfo:[]
    }

    componentWillMount(){
        const {movieInfo} = this.state
        let movieId = sessionStorage.getItem('movieId');
        let page = sessionStorage.getItem('Page');
         let url
         url = `${api}/${page}/${movieId}?api_key=${apiKey}&append_to_response=videos,images`
        
         fetch(url)
         .then((response) => {
             response.json().then((data) => {
                 console.log(data)
             let info
             if (response.status === 200) {
             info = {
                tagline: data.tagline,
                poster: data.poster_path,
                genres: data.genres,
                title: data.title,
                name:data.name,
                status: data.status,
                overview: data.overview,
                rating: data.vote_average,
                companies: data.production_companies,
                release: data.release_date,
                background: data.backdrop_path
             }
            }else{
                info={name: "Sorry couldn't retrieve any data"}
            }
             movieInfo.push(info)
             this.setState({movieInfo})
         })
        })
     
    }

    render(){
        const {movieInfo} = this.state
        let poster,
            title,
            name,
            tagline,
            overview,
            genres,
            companies,
            release,
            background

       movieInfo.map((info) => (
            title = info.title,
            name = info.name,
            tagline = info.tagline,
            overview = info.overview,
            genres = info.genres.map((genre) => (
            genre.name
           )).join(', '),
           companies = info.companies.map((company) => (
            company.name
           )).join(', '),
           release = info.release,
           background = `https://image.tmdb.org/t/p/original${info.background}`,
           poster = `https://image.tmdb.org/t/p/w500${info.poster}`
        ))
      
        
        const styles = {width: '100%', height: '100%', backgroundImage:background ? (`url(${background})`) : null }
        return(
            <div>
                <section style={styles} className="infoSection ">
               <div>
                <div className="imageBox"> 
                <div className="poster" sm="12" md="7" lg="5">
                    <div className="img"><img src={poster}/></div>
                </div>
                    <div className="info" sm="12" md="5" lg="7">
                        <h1>{title}{name}</h1>
                        <h2>{tagline}</h2>
                        <p>{overview}</p>
                        <h2>{genres}</h2>
                        <h2>{companies}</h2>
                        <h2>{release}</h2>
                    </div>

                </div>
               </div>
                </section>
            </div>
        )
    }
}


export default MovieInfo;



